<template>
  <div>
    <section class="content-header">
      <h1>Get a Quote</h1>
    </section>
    <section class="content vld-parent" ref="ref_load_quotes">
      <form @submit.prevent="submit_quote('submit')" data-vv-scope="frmStep1">
        <div class="card">
          <div class="box-header with-border">
            <h3 class="box-title card-text">User </h3>
          </div>
          <div class="box-body ">
            <div class="row">
              <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="form-group">
                  <label class="required">Name</label>
                  <input type="text" class="form-control" placeholder="Enter Name ..." v-model="frm_quotes.name"
                    v-validate="'required'" data-vv-name="name"
                    :class="{ 'error': errors.name, 'error': verrors.has('name'), 'haveValue': frm_quotes.name }">
                  <p v-if="verrors.has('frmStep1.name')" class="errormsg"> {{
                      verrors.first('frmStep1.name')
                  }}</p>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="form-group">
                  <label class="required">Company Name </label>
                  <input type="text" class="form-control" placeholder="Enter Company ..."
                    v-model="frm_quotes.organization" v-validate="'required'" data-vv-name="organization"
                    :class="{ 'error': errors.organization, 'error': verrors.has('organization'), 'haveValue': frm_quotes.organization }">
                  <p v-if="verrors.has('frmStep1.organization')" class="errormsg"> {{
                      verrors.first('frmStep1.organization')
                  }}</p>
                </div>
              </div>
              <div class="clearfix"></div>
              <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="form-group">
                  <label class="required">Work Email </label>
                  <input type="text" class="form-control" placeholder="Enter Email ..." v-model="frm_quotes.email"
                    v-validate="'required|email'"
                    :class="{ 'error': errors.email, 'error': verrors.has('email'), 'haveValue': frm_quotes.email }"
                    data-vv-name="email" data-vv-scope="frmStep1">
                  <p v-if="verrors.has('frmStep1.email')" class="errormsg"> {{ verrors.first('frmStep1.email') }}</p>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="form-group">
                  <label class="required">Contact Number</label>
                  <input type="text" class="form-control" placeholder="Enter Phone ..." v-model="frm_quotes.phone"
                    v-validate="'required'" data-vv-name="phone" data-vv-scope="frmStep1" data-vv-as="phone no"
                    :class="{ 'error': errors.phone, 'error': verrors.has('phone'), 'haveValue': frm_quotes.phone }">
                  <p v-if="verrors.has('frmStep1.phone')" class="errormsg"> {{ verrors.first('frmStep1.phone') }}</p>
                </div>
              </div>
              <div class="clearfix"></div>
              <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                <div class="form-group">
                  <label class="required">Address</label>
                  <input type="text" class="form-control" placeholder="Enter Address ..." v-model="frm_quotes.address_1"
                    v-validate="'required'" data-vv-name="address_1" data-vv-scope="frmStep1" data-vv-as="address 1"
                    :class="{ 'error': errors.address_1, 'error': verrors.has('address_1'), 'haveValue': frm_quotes.address_1 }">
                  <p v-if="verrors.has('frmStep1.address_1')" class="errormsg"> {{ verrors.first('frmStep1.address_1')
                  }}
                  </p>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                <div class="form-group">
                  <label class="required">City</label>
                  <input type="text" class="form-control" placeholder="Enter City ..." v-model="frm_quotes.city"
                    v-validate="'required'" data-vv-name="city" data-vv-scope="frmStep1"
                    :class="{ 'error': errors.city, 'error': verrors.has('city'), 'haveValue': frm_quotes.city }">
                  <p v-if="verrors.has('frmStep1.city')" class="errormsg"> {{ verrors.first('frmStep1.city') }}
                  </p>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                <div class="form-group">
                  <label class="required">State</label>
                  <input type="text" class="form-control" placeholder="Enter State ..." v-model="frm_quotes.state"
                    v-validate="'required'"
                    :class="{ 'error': errors.state, 'error': verrors.has('state'), 'haveValue': frm_quotes.state }"
                    data-vv-name="state" data-vv-scope="frmStep1">
                  <p v-if="verrors.has('frmStep1.state')" class="errormsg"> {{ verrors.first('frmStep1.state') }}
                  </p>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                <div class="form-group">
                  <label class="required">Zip Code</label>
                  <input type="text" class="form-control" placeholder="Enter ZIP ..." v-model="frm_quotes.zip_code"
                    v-validate="'required'" data-vv-as="zip code"
                    :class="{ 'error': errors.zip_code, 'error': verrors.has('zip_code'), 'haveValue': frm_quotes.zip_code }"
                    data-vv-name="zip_code" data-vv-scope="frmStep1">
                  <p v-if="verrors.has('frmStep1.zip_code')" class="errormsg"> {{ verrors.first('frmStep1.zip_code') }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card">
          <div class="box-header with-border">
            <div class="pull-left">
              <h3 class="box-title card-text">Pickup Location</h3>
            </div>
            <div class="pull-right">
              <input type="checkbox" @change="check($event)"> Same as office
            </div>
            <div class="clearfix"></div>
          </div>
          <div class="box-body ">
            <div class="row">
              <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="form-group">
                  <label class="required">Address 1</label>
                  <input type="text" class="form-control" placeholder="Enter address ..."
                    v-model="frm_quotes.pickup_address_1" v-validate="'required'" data-vv-as="pickup address 1"
                    :class="{ 'error': errors.pickup_address_1, 'error': verrors.has('pickup_address_1'), 'haveValue': frm_quotes.pickup_address_1 }"
                    data-vv-name="pickup_address_1" data-vv-scope="frmStep1">
                  <p v-if="verrors.has('frmStep1.pickup_address_1')" class="errormsg"> {{
                      verrors.first('frmStep1.pickup_address_1')
                  }}</p>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="form-group">
                  <label>Address 2</label>
                  <input type="text" class="form-control" placeholder="Enter address ..."
                    v-model="frm_quotes.pickup_address_2">
                </div>
              </div>
              <div class="clearfix"></div>
              <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                <div class="form-group">
                  <label class="required">City</label>
                  <input type="text" class="form-control" placeholder="Enter City ..." v-model="frm_quotes.pickup_city"
                    v-validate="'required'"
                    :class="{ 'error': errors.pickup_city, 'error': verrors.has('pickup_city'), 'haveValue': frm_quotes.pickup_city }"
                    data-vv-name="pickup_city" data-vv-scope="frmStep1" data-vv-as="pickup city">
                  <p v-if="verrors.has('frmStep1.pickup_city')" class="errormsg"> {{
                      verrors.first('frmStep1.pickup_city')
                  }}
                  </p>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                <div class="form-group">
                  <label class="required">State</label>
                  <input type="text" class="form-control" placeholder="Enter State ..."
                    v-model="frm_quotes.pickup_state" v-validate="'required'"
                    :class="{ 'error': errors.pickup_state, 'error': verrors.has('pickup_state'), 'haveValue': frm_quotes.pickup_state }"
                    data-vv-name="pickup_state" data-vv-scope="frmStep1" data-vv-as="pickup state">
                  <p v-if="verrors.has('frmStep1.pickup_state')" class="errormsg"> {{
                      verrors.first('frmStep1.pickup_state')
                  }}
                  </p>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                <div class="form-group">
                  <label class="required">Zip Code</label>
                  <input type="text" class="form-control" placeholder="Enter ZIP ..."
                    v-model="frm_quotes.pickup_zip_code" v-validate="'required'"
                    :class="{ 'error': errors.pickup_zip_code, 'error': verrors.has('zip_code'), 'haveValue': frm_quotes.pickup_zip_code }"
                    data-vv-name="pickup_zip_code" data-vv-scope="frmStep1" data-vv-as="pickup zip code">
                  <p v-if="verrors.has('frmStep1.pickup_zip_code')" class="errormsg"> {{
                      verrors.first('frmStep1.pickup_zip_code')
                  }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card">
          <div class="box-header with-border">
            <h3 class="box-title card-text">Scrap Information</h3>
          </div>
          <div class="box-body">
            <div class="tablesection">
              <div class="">
                <table class="table table-striped table-hover">
                  <thead>
                    <tr>
                      <th style="text-align: center;" width="70">Sl No.</th>
                      <th>Material</th>
                      <th>Weight (LB) </th>
                      <th width="100">Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, ix) in frm_quotes.material" :key="'up' + ix">
                      <td align="center">{{ ix + 1 }}</td>
                      <td>
                        <v-select :reduce="(option) => option.id" :options="products" :label="'name'"
                          placeholder="Search Product ..." v-model="item.product_id" v-validate="'required'"
                          :class="{ 'error': errors.state, 'error': verrors.has('products'), 'haveValue': frm_quotes.products }"
                          :name="'product_id' + ix" data-vv-as="product" data-vv-scope="frmStep1" />
                        <p v-if="verrors.has('frmStep1.product_id' + ix)" class="errormsg"> {{
                            verrors.first('frmStep1.product_id' + ix)
                        }}</p>
                      </td>
                      <td>
                        <input class="form-control" v-model="item.weight" type="text" v-validate="'required'"
                          :name="'weight' + ix" data-vv-scope="frmStep1" data-vv-as="weight" />
                        <p v-if="verrors.has('frmStep1.weight' + ix)" class="errormsg"> {{
                            verrors.first('frmStep1.weight' + ix)
                        }}</p>
                      </td>
                      <td>
                        <button type="button" class="btn btn-danger" v-if="ix > 0"
                          @click="frm_quotes.material.splice(ix, 1)"><i aria-hidden="true"
                            class="fa fa-trash-o"></i></button>
                        <button type="button" class="btn btn-success" v-if="frm_quotes.material.length == ix + 1"
                          @click="addInfo()"><i aria-hidden="true" class="fa fa-plus"></i></button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <br>
            <div class="row">
              <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="form-group">
                  <label>Additional Information</label>
                  <textarea class="form-control" placeholder="Enter Additional Information"
                    v-model="frm_quotes.additional_information"></textarea>
                </div>
              </div>
              <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                <div class="form-group">
                  <label>Attach scrap metal pictures (If Any)</label>
                  <input type="file" accept="image/*" @change="onFileChange" />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="card box-success">
          <div class="box-footer">
            <div class="text-center">
              <button type="submit" class="btn link1 mx-1">Get Quote</button>
            </div>
          </div>
        </div>
      </form>
    </section>
  </div>
</template>

<script>
import {
  mapGetters
} from "vuex";
export default {
  name: "QuotesAddEdit",
  data() {
    return {
      user_id: '2',
      view_mode: false,
      editmode: false,
      products: [],
      step: 1,
      frm_quotes: new Form({
        created_by: 2,
        name: '',
        organization: '',
        email: '',
        phone: '',
        address_1: '',
        city: '',
        state: '',
        zip_code: '',
        pickup_address_1: '',
        pickup_address_2: '',
        pickup_city: '',
        pickup_state: '',
        pickup_zip_code: '',
        additional_information: '',
        scrap_material_picture: null,
        scrap_material_picture_url: null,
        material: []
      })
    }
  },
  computed: {
    ...mapGetters(["errors"])
  },
  beforeCreate() {
    axios.get("api/getProducts")
      .then((res) => {
        this.products = res.data.data
      })
  },
  methods: {
    async addInfo() {
      this.frm_quotes.material.push({
        id: '',
        product_id: '',
        weight: '',
      })
    },
    submit_quote() {
      this.$validator.validateAll('frmStep1').then(async (valid) => {
        if (valid) {
          let cloaderd = this.$loading.show({
            container: this.$refs.ref_load_quotes
          });

          this.frm_quotes.post('api/quotes')
            .then((data) => {
              cloaderd.hide();
              console.log(data);
              this.frm_quotes.id = data.data.data.id;
              this.frm_quotes.material = []
              this.addInfo()

              Toast.fire({
                icon: 'success',
                title: data.message
              });

            })
            .catch(() => {
              cloaderd.hide();
            })
        }
      })

    },

    check(e) {
      this.$nextTick(() => {
        if (e.target.checked == true) {
          this.frm_quotes.pickup_address_1 = this.frm_quotes.address_1;
          this.frm_quotes.pickup_city = this.frm_quotes.city;
          this.frm_quotes.pickup_state = this.frm_quotes.state;
          this.frm_quotes.pickup_zip_code = this.frm_quotes.zip_code;
        } else {
          this.frm_quotes.pickup_address_1 = '';
          this.frm_quotes.pickup_city = '';
          this.frm_quotes.pickup_state = '';
          this.frm_quotes.pickup_zip_code = '';
        }
      })
    },
    onFileChange(e) {
      const file = e.target.files[0]
      this.scrap_material_picture = file
      this.frm_quotes.scrap_material_picture_url = URL.createObjectURL(file)
    }
  },
  mounted() {
    let cloaderd = this.$loading.show({
      container: this.$refs.ref_load_quotes
    });
    if (this.$route.params.id) {
      axios.get("api/quotes/" + this.$route.params.id)
        .then((res) => {
          console.log(res);
          this.frm_quotes.name = res.data.data.name;
          this.frm_quotes.organization = res.data.data.organization;
          this.frm_quotes.email = res.data.data.email;
          this.frm_quotes.phone = res.data.data.phone;
          this.frm_quotes.address_1 = res.data.data.address;
          this.frm_quotes.city = res.data.data.city;
          this.frm_quotes.state = res.data.data.state;
          this.frm_quotes.zip_code = res.data.data.zip;
          this.frm_quotes.pickup_address_1 = res.data.data.pickup_address_1;
          this.frm_quotes.pickup_address_2 = res.data.data.pickup_address_2;
          this.frm_quotes.pickup_city = res.data.data.pickup_city;
          this.frm_quotes.pickup_state = res.data.data.pickup_state;
          this.frm_quotes.picup_zip_code = res.data.data.pickup_zip;
          this.frm_quotes.additional_information = res.data.data.additional_information;
          this.frm_quotes.material = res.data.data.quote_products;

          cloaderd.hide();
        })
        .catch(() => {
          cloaderd.hide();
        })
    } else {
      axios.get("api/user/" + this.user_id)
        .then((res) => {
          console.log(res);
          this.frm_quotes.name = res.data.data.name;
          this.frm_quotes.organization = res.data.data.organization;
          this.frm_quotes.email = res.data.data.email;
          this.frm_quotes.phone = res.data.data.phone;
          this.frm_quotes.address_1 = res.data.data.address_1;
          this.frm_quotes.city = res.data.data.city;
          this.frm_quotes.state = res.data.data.state;
          this.frm_quotes.zip_code = res.data.data.zip_code;
          cloaderd.hide();
        })
        .catch(() => {
          cloaderd.hide();
        })
    }

  },
  created() {
    this.step = 1
    this.$Progress.start();
    this.view_mode = false
    this.addInfo();
    this.$Progress.finish();
  },
  watch: {
  }
}
</script>
